@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap"); /* http://meyerweb.com/eric/tools/css/reset/
   v5.0.1 | 20191019
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
menu,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
main,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section {
  display: block;
}

/* HTML5 hidden-attribute fix for newer browsers */
*[hidden] {
  display: none;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  line-height: 1;
}

menu,
ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

body {
  font-family: "Roboto", sans-serif;
}

.adxLogo {
  display: block;
  position: absolute;
  right: 0;
}

.card {
  height: 189px;
  display: flex;
  position: relative;
  margin: 20px 0;
  background-color: white;
}
.card__image {
  padding: 21px 0;
  padding-right: 20px;
}
.card__content {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-right: 5px;
  padding-bottom: 5px;
}
.card__body {
  margin-top: 17px;
}
.card__body--title {
  font-size: 18px;
  font-weight: 700;
  line-height: 23px;
  color: initial;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.card__body--link {
  font-size: 12px;
  font-weight: 300;
  line-height: 18px;
  color: #a5a5a5;
}
.card__body--description {
  font-size: 13px;
  font-weight: 400;
  line-height: 18px;
  color: #868686;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.card__footer {
  margin-top: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.card__footer--left {
  display: flex;
}
.card__footer--left > *:not(:last-child) {
  margin-right: 5px;
}
.card__footer--sponsor-name {
  font-size: 14px;
  font-weight: 700;
  line-height: 18px;
  color: #1c7ddc;
}
.card__footer--text {
  align-self: center;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  color: #a5a5a5;
}
.card__footer--goto {
  height: 30px;
  width: 30px;
}

.box {
  background-color: white;
  height: 300px;
  width: 660px;
  margin: auto;
  margin-top: 10px;
}
.box__top {
  display: flex;
  padding: 15px 15px 0 15px;
}
.box__top-title {
  font-size: 18px;
  font-weight: 700;
  line-height: initial;
  color: #236c62;
  margin-top: auto;
}
.box__bottom {
  display: flex;
  margin-top: 18px;
}
.box__bottom--left, .box__bottom--right {
  margin-left: 18px;
}
.box__bottom--right {
  display: flex;
  flex-direction: column;
  margin-right: 20px;
  padding-top: 20px;
  padding-left: 15px;
  padding-right: 10px;
  height: 185px;
  position: relative;
  justify-content: space-between;
  align-items: center;
  border-radius: 10px;
  background: #fff;
  box-shadow: 0px 0px 7px 0px rgba(136, 136, 136, 0.25);
}
.box__bottom--left {
  width: 304px;
  height: 171px;
  background: #e5e5e5;
  box-shadow: 0px 4px 4px 0px rgba(185, 185, 185, 0.25);
}
.box__bottom--left img {
  object-fit: contain;
}
.box__bottom--left p {
  font-size: 10px;
  font-weight: 400;
  line-height: normal;
  color: #656565;
}
.box__info {
  display: flex;
}
.box__info--left {
  margin-right: 15px;
}
.box__info--left img {
  object-fit: contain;
  height: 123px;
  width: 123px;
}
.box__info--right {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.box__info--right img {
  width: 82px;
  height: 30px;
}
.box__info-title {
  font-size: 14px;
  font-weight: 500;
  line-height: 18.2px;
  color: initial;
}
.box__info-desc {
  font-size: 13px;
  font-weight: 400;
  line-height: 15.6px;
  color: initial;
}
.box__info-button {
  position: absolute;
  bottom: 15px;
  width: 110px;
  height: 23px;
}
.box__info-button button {
  border: none;
  height: 100%;
  width: 100%;
  border-radius: 10px;
  background: var(--dr-green-2, radial-gradient(259.48% 104.13% at 45.83% 54.55%, rgba(11, 24, 27, 0.5) 0%, rgba(24, 121, 104, 0.5) 51.28%, rgba(0, 200, 164, 0.5) 100%), #20c8a7);
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
  font-size: 10px;
  font-weight: 800;
  line-height: 12px;
  color: #fff;
}

body {
  background-color: black;
}